.user_profile::after {
    content: "";
    position: absolute;
    left: 200%;
    top: -50%;
    width: 200%;
    height: 250%;
    background: linear-gradient(180deg,rgba(253, 253, 253, 0.2) 0,#dadada59 20%,#bbbbbb57 45%,#d6d6d64d 55%,#d3d3d354 50%,rgba(199, 199, 199, 0.2) 60%);
    -webkit-transform: matrix(.47,-.97,.77,.53,0,0);
            transform: matrix(.47,-.97,.77,.53,0,0);
    -webkit-animation: blink 2s infinite linear;
            animation: blink 2s infinite linear;
  }
  
  @-webkit-keyframes blink {
    from {
      right: 0;
    }
    to {
      left: -150%;
    }
  }
  
